import '@/assets/css/video.css'

export default {
  name: "index",
  destroyed() {
    this.listener_init.off();
  },
  created() {
    window._t = this;
    console.log('create topics index');
    this.init();
    this.listener_init = this.$hub.on('event:init', () => {
      console.log('call event:init');
      this.init();
      this.$forceUpdate();
    });
  },
  methods: {
    on_select_forum: function (forum) {
      var forum_url = '/forum/' + forum.fid;
      this.$router.push(forum_url);
    },
    init: function () {
      this.update_threads();
      if (this.carousel_threads && this.carousel_threads.length > 0) {
        this.slide = this.carousel_threads[0]['id'];
      }
    },
    update_threads: function () {
      this.last_threads = this.get_last_threads();
      this.indextop_threads = this.get_indextop_threads();
      this.carousel_threads = this.get_carousel_threads();
    },
    get_last_threads: function () {
      try {
        if (this.$store.state.init.data.last_threads.datas.length > 0) {
          return this.$store.state.init.data.last_threads.datas;
        }
      } catch (e) {

      }
      return undefined
    },
    get_indextop_threads: function () {
      try {
        if (this.$store.state.init.data.indextop_threads.datas.length > 0) {
          return this.$store.state.init.data.indextop_threads.datas;
        }
      } catch (e) {

      }
      return undefined
    },
    get_carousel_threads: function () {
      try {
        if (this.$store.state.init.data.carousel_threads.datas.length > 0) {
          return this.$store.state.init.data.carousel_threads.datas;
        }
      } catch (e) {

      }
      return undefined
    }
  },
}
